.footer__content-icon {
  width: 300px;
  height: 100px;
  margin: 20px auto 0;
  display: flex;
  align-items: center;
  gap: 30px;
}

@media screen and (min-width: 320px) and (max-width: 768px) {
  .footer__content-icon {
    width: 250px;
    margin: 0;
    justify-content: flex-start;
  }
}
