.paragraph {
  width: 90%;
  color: white;
  font-weight: 300;
  font-size: 20px;
  margin: 30px auto 0;
  line-height: 1.6;
  text-align: justify;
}

@media screen and (min-width: 320px) and (max-width: 768px) {
  .paragraph {
    font-size: 12px;
    margin: 15px auto 0;
    width: 60%;
  }
}
