.btn {
  background-color: #9553fd;
  border: none;
  color: white;
  width: 250px;
  height: 55px;
  font-weight: 600;
  font-size: 26px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease-in;
}

.btn:hover {
  background-color: white;
  color: black;
  box-shadow: -10px 10px 10px 0px rgba(0, 0, 0, 0.5);
}

@media screen and (min-width: 320px) and (max-width: 768px) {
  .btn {
    width: 150px;
    height: 40px;
    margin: 0 auto;
    font-size: 20px;
  }
}
